import React from 'react';
import Helmet from 'react-helmet';
import {Route} from 'react-router-dom';
import Tutorial1 from '../../Content/Tutorials/Tutorial1';
import TutorialList from './TutorialList';
import Disqus from 'disqus-react';
const Tutorials = () => {
    const disqusShortname = 'marofe-github-io';
        const disqusConfig = {
            url: process.env.PUBLIC_URL+'/#/tutorials',
            identifier: 'tutorials',
            title: 'Tutorials'
        };
        console.log(process.env.PUBLIC_URL+'/#tutorials');
    return <div className="divPages">
    <Helmet>
        <title>Tutorials | Marcos Rogério Fernandes</title>
        <meta name="description" content="Welcome to my personal website. Here you will find my reserach interest and contributions. " />
    </Helmet>
        <div className="top">
        <h1>Tutorials</h1>
        </div>
        
     <Route path="/tutorials" exact render={(props) => <TutorialList/>}/>
    <Route path="/tutorials/rastreamento_usando_visao_filtro_kalman" exact render={(props) => <Tutorial1/>}/>
    <Disqus.DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
    </div>
}
export default Tutorials;